<template>
  <div @click.self="close" class="z-10 fixed inset-0 bg-black bg-opacity-60">
    <div
      class="
        md:mt-20
        opacity-100
        mx-auto
        bg-white
        dark:bg-gray-800
        w-full
        md:max-w-lg
        h-screen
        md:h-auto
        p-4
        md:rounded
        shadow-lg
      "
    >
      <h2 class="font-bold text-center text-lg mb-5">Settings</h2>
      <div class="divide-y divide-gray-200">
        <label class="py-2"
          ><!-- TODO could pull this out -->
          <input type="checkbox" id="dark-mode" v-model="value.dark" />
          <span class="ml-2">Dark mode</span>
        </label>
        <label class="flex py-2 mt-5">
          <span class="self-center w-2/3">Number of sprints</span>
          <div class="w-1/3 border-2 rounded p-1">
            <input
              type="number"
              min="1"
              max="30"
              v-model.number="value.totalSessions"
            />
          </div>
        </label>
        <label class="flex py-2">
          <span class="self-center w-2/3">Length of each sprint</span>
          <div class="w-1/3 border-2 rounded p-1">
            <input
              type="number"
              min="1"
              max="30"
              v-model.number="sprintLength"
            />
            mins
          </div>
        </label>
        <label class="flex py-2">
          <span class="self-center w-2/3"
            >Length of pause between sessions</span
          >
          <div class="w-1/3 border-2 rounded p-1">
            <input
              type="number"
              min="1"
              max="30"
              v-model.number="pauseLength"
            />
            mins
          </div>
        </label>
        <label class="flex py-2">
          <span class="self-center w-2/3"
            >Delete text after a pause of</span
          >
          <div class="w-1/3 border-2 rounded p-1">
            <input
              type="number"
              min="5"
              max="60"
              v-model.number="value.deleteAfter"
            />
            seconds
          </div>
        </label>
      </div>
      <div class="mt-5 flex w-full justify-end">
        <button
          class="bg-green-300 dark:bg-green-800 p-2 px-5 rounded"
          @click="close"
        >
          Done
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Settings',

  data() {
    return {
      editor: null,
    };
  },

  methods: {
    close() {
      this.$emit('close');
    },
  },

  computed: {
    sprintLength: {
      get() {
        return Math.round(this.value.sprintLength / 60);
      },
      set(val) {
        this.value.sprintLength = val * 60;
      },
    },
    pauseLength: {
      get() {
        return Math.round(this.value.pauseLength / 60);
      },
      set(val) {
        this.value.pauseLength = val * 60;
      },
    },
  },

  watch: {
    value: {
      deep: true,
      handler() { this.$emit('input', this.value); },
    },
  },

  props: {
    value: {
      type: Object,
    },
  },
};
</script>

<style>
input {
  @apply dark:bg-gray-800;
}
</style>
