<template>
  <div id="app" :class="settings.dark ? 'dark' : ''">
    <div
      class="
        h-screen
        bg-white
        dark:bg-gray-800
        text-gray-800
        dark:text-gray-100
        p-4
        md:p-10
        flex
      "
    >
      <Settings
        @close="showSettings = false"
        :class="showSettings ? 'block' : 'hidden'"
        v-model="settings"
      />
      <div class="flex justify-between gap-4 w-full">
        <!-- left -->
        <p class="p-2 w-1/6 md:block hidden"></p>
        <!-- middle -->
        <div class="w-full md:max-w-screen-md flex flex-col">
          <div class="flex">
            <Mode
              class="flex-grow"
              :mode="currentMode"
              :secondsLeft="secondsLeft"
              :session="currentSession"
              :totalSessions="settings.totalSessions"
              @reset="reset"
            />
            <button
              :disabled="started"
              @click="toggleSettings"
              :class="'md:hidden pl-4 ' + (started ? 'opacity-50 cursor-default' : '')"
            >
              <icon icon="cog" />
            </button>
          </div>
          <Editor
            class="filter flex-grow overflow-auto"
            :style="editorStyle"
            v-model="content"
            @wc="updateWordCount"
            :placeholder="placeholder"
          />
          <div
            class="
              mx-auto
              w-full
              md:max-w-screen-md
              flex
              items-end
              justify-between
              p-4
            "
          >
            <div class="text-blue-300 dark:text-gray-700 text-sm">
              {{ version }}<span class="hidden">{{ fullVersion }}</span>
            </div>
            <div class="font-bold">{{ wc }} words</div>
            <div class="text-blue-300 dark:text-gray-700 text-sm">
              <a href="https://twitter.com/egocv">contact @egocv</a>
            </div>
          </div>
        </div>
        <!-- right -->
        <div class="w-1/6 py-4 content-end hidden md:block">
          <button class="pr-4" @click="toggleDarkMode">
            <icon :icon="settings.dark ? 'sun' : 'moon'" />
          </button>
          <a
            class="pr-4"
            target="_blank"
            href="https://egocv.com/write-dash-app.html"
            ><icon icon="question-circle"
          /></a>
          <button :disabled="started" @click="toggleSettings" :class="started ? 'opacity-50 cursor-default' : ''">
            <icon icon="cog" />
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Editor from './components/Editor.vue';
import Mode from './components/Mode.vue';
import Settings from './components/Settings.vue';
import modes from './services/modes';

export default {
  name: 'App',
  components: {
    Editor,
    Mode,
    Settings,
  },

  data() {
    return {
      content: '<p></p>',
      wc: 0,
      settings: {
        dark: false,
        sprintLength: 3 * 60,
        pauseLength: 1 * 60,
        totalSessions: 2,
        deleteAfter: 8,
      },
      sessionStart: 0,
      state: '',
      timePassed: null, // manually updated
      lastEdit: 0,
      showSettings: false,
    };
  },

  created() {
    setInterval(() => {
      this.setTimePassed();
    }, 100);
  },

  mounted() {
    // when all child components are loaded
    this.$nextTick(() => window.scrollTo(0, 1)); // hide url bar on mobile
  },

  computed: {
    placeholder() {
      const writeMins = Math.round(this.settings.sprintLength / 60);
      const editMins = Math.round(this.settings.pauseLength / 60);
      return `Your session will begin once you start writing!

Each session will be ${writeMins} minutes of writing followed by a ${editMins} minute break
Don't pause for too long during a sprint, or your words will fade and be GONE FOREVER.
F11 to open full screen, ctrl-b for bold, ctrl-i for italics, mardown headers, etc.`;
    },
    version() {
      return process.version.tag;
    },
    fullVersion() {
      return process.version.raw;
    },
    secondsPassed() {
      return Math.floor(this.timePassed / 1000);
    },
    sessionLength() {
      return this.settings.sprintLength + this.settings.pauseLength;
    },
    currentSession() {
      return Math.ceil(this.secondsPassed / this.sessionLength) || 1;
    },
    currentMode() {
      if (this.state === 'died') {
        return modes.DIED;
      }
      if (this.state === 'complete') {
        return modes.COMPLETE;
      }

      if (!this.sessionStart) {
        return modes.READY;
      }
      return this.secondsPassed % this.sessionLength <= this.settings.sprintLength
        ? modes.WRITING
        : modes.EDITING;
    },
    started() {
      return (
        this.currentMode === modes.WRITING || this.currentMode === modes.EDITING
      );
    },
    blurAfter() {
      return Math.round(this.settings.deleteAfter / 2);
    },
    secondsLeft() {
      const secondsPassed = this.secondsPassed % this.sessionLength;
      if (this.currentMode === modes.WRITING) {
        return this.settings.sprintLength - secondsPassed;
      }
      return this.sessionLength - secondsPassed;
    },
    editorStyle() {
      if (this.currentMode === modes.WRITING) {
        const secondsSinceLastEdit = (Date.now() - this.lastEdit) / 1000;
        const maxBlur = 7;
        if (secondsSinceLastEdit > this.blurAfter) {
          const blurPct = (secondsSinceLastEdit - this.blurAfter)
            / (this.settings.deleteAfter - this.blurAfter); // % of total blur
          return `--tw-blur: blur(${maxBlur * blurPct}px)`;
        }
      }
      return '';
    },
  },

  methods: {
    toggleDarkMode() {
      this.settings.dark = !this.settings.dark;
    },

    toggleSettings() {
      this.showSettings = !this.showSettings;
    },

    reset() {
      this.state = '';
      this.sessionStart = 0;
    },

    startSession() {
      this.sessionStart = Date.now();
      this.state = 'running';
    },

    setTimePassed() {
      if (this.started) {
        this.timePassed = Date.now() - this.sessionStart;

        // skip the last edit session
        if (
          this.timePassed / 1000
        > this.settings.totalSessions * this.sessionLength - this.settings.pauseLength
        ) {
          this.state = 'complete';
        }

        const secondsSinceLastEdit = this.getSecondsSinceLastEdit();
        if (secondsSinceLastEdit > this.settings.deleteAfter) {
          this.delete();
        }
      }
    },

    getSecondsSinceLastEdit() {
      return this.currentMode === modes.WRITING
        ? (Date.now() - this.lastEdit) / 1000
        : 0;
    },

    delete() {
      this.content = '<p></p>';
      this.state = 'died';
    },

    modeChanged() {
      this.lastEdit = Date.now();
    },

    updateWordCount(wc) {
      this.wc = wc;
    },
  },

  watch: {
    currentMode(to, from) {
      this.modeChanged(to, from);
    },
    content() {
      // start session when content changes
      if (this.sessionStart === 0) {
        this.startSession();
      }
      this.lastEdit = Date.now();
    },
  },
};
</script>
